dark .s6bnemm .card-header{background-color:#303437;border-color:#545b62;color:var(--light);}.theme-dark .s6bnemm .card-header::after{background:linear-gradient(90deg,rgba(48,52,55,0) 0%,rgba(48,52,55,1) 50%);}
